.wrapper {
    width: 100%;
    height: 88%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    .filterForMobile {
        display: none;
        height: 100%;
        width: 100%;
        background-color: #52C7FF;
        margin-left: 10px;
        border-radius: 30px;
        padding: 20px;
        button {
            border: 0;
            cursor: pointer;
        }
        h1 {
            text-align: center;
            color: white;
            font-size: 16px;
            font-weight: bold; 
        }
        .price {
            width: 100%;
            background-color: white;
            margin-top: 20px;
            padding: 5px;
            &:hover {
                background-color: rgb(223, 223, 223);
            }
            h1 {
                color: black;
                font-size: 16px;
                font-weight: bold; 
                
            }
        }
        .color {
            width: 100%;
            background-color: white;
            margin-top: 20px;
            padding: 5px;
            &:hover {
                background-color: rgb(223, 223, 223);
            }
