                font-size: 16px;
                font-weight: bold; 
            }
        }
        .material {
            width: 100%;
            background-color: white;
            margin-top: 20px;
            padding: 5px;
            transition: 0.3s;
            &:hover {
                background-color: rgb(223, 223, 223);
            }
            h1 {
                color: black;
                font-size: 16px;
                font-weight: bold; 
            }
        }
        .containerInputsPrice {
            width: 100%;
            display: flex;
            flex-flow: column wrap;
            justify-content: center;
            align-items: center;
            margin-top: 5px;
            .containerPrice {
                display: flex;
                justify-content: space-around;
                align-items: center;
                input {
                    width: 40%;
                    text-align: center;
                    border: 1px solid white;
                    color: black;
                    text-align: center;
                    font-size: 16px;
                    font-weight: bold; 
                    margin-bottom: 5px;
                    &:focus {
                        outline: 0;
                    }
                }
            }
            button {
                width: 100%;
                color: white;
                background-color: #52C7FF;
                border: 1px solid white;
                border-radius: 30px;
                font-size: 16px;
                font-weight: bold;
                padding: 5px;
                margin-top: 5px;
                cursor: pointer;
