    .body {
        width: 100%;
        height: 100%;
        background-color: #52C7FF;
        border-radius: 30px;
        padding: 20px;
        display: flex;
        flex-flow: column wrap;
        justify-content: flex-start;
        align-items: center;
        .search__body {
            width: 100%;
            height: fit-content;
            padding: 10px;
            .search__container {
                width: 100%;
                background-color: white;
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-radius: 30px;
                padding: 10px 15px 10px 15px;
                input {
                    font-size: 16px;
                    font-weight: bold;
                    border: 0;
                    width: 100%;
                    padding-right: 15px;
                    &:focus {
                        outline: 0;
                    }
                }
                button {
                    color: white;
                    background-color: #52C7FF;
                    border: 0;
                    border-radius: 30px;
                    font-size: 16px;
                    font-weight: bold;
                    padding: 10px;
                    cursor: pointer;
                    transition: 0.3s;
                    &:hover {
                        background-color: #30a1d6;
                    }
                }
                .iconFilter {
                    display: none;
                    height: 35px;
                    width: 35px;
                    border-radius: 12px;
                    background-size: cover;
                    background-position: center;
                    background-image: url(../Assets/filterIcon.png);
                }
            }
        }
        .posts {
            width: 100%;
            height: 90%;
            background-color: white;
            border-radius: 30px;
            padding: 20px 10px 20px 10px;
            .container__posts {
                height: 100%;
                width: 100%;
                display: flex;
                flex-flow: row wrap;
                align-items: flex-start;
                justify-content: center;
                align-content: flex-start;
                overflow-y: scroll;
                &::-webkit-scrollbar {
                    width: 0;
                }
                  
                &::-webkit-scrollbar-thumb {
                    background-color: #52C7FF;
                }
                .post__body {
                    width: 150px;
                    margin: 0 20px 20px 20px;
                    button {
                        border: 0;
                        background: transparent;
                        cursor: pointer;
                        transition: 0.3s;
                        &:hover {
                            background-color: #52C7FF;
                        }
                        .postImage1 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/purpleBagForGirlsBlueface.jpg);
                            margin-bottom: 10px;
                        }
                        .postImage2 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/redBagForGirlsOrigpunch.jpg);
                            margin-bottom: 10px;
                        }
                        .postImage3 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/blackPortmoneForManMorelli.jpg);
                            margin-bottom: 10px;
                        }
                        .postImage4 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/purpleBagForGirlsMorelli.jpeg);
                            margin-bottom: 10px;
                        }
                        .postImage5 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/blueBagForGirlsBlureedition.jpeg);
                            margin-bottom: 10px;
                        }
                        .postImage6 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/purpleBagForGirlsBluefaceSecond.jpg);
                            margin-bottom: 10px;
                        }
                        .postImage7 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/redBagForGirlsOrigpunchSecond.jpg);
                            margin-bottom: 10px;
                        }
                        .postImage8 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/blackPortmoneForManMorelliSecond.jpg);
                            margin-bottom: 10px;
                        }
                        .postImage9 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/purpleBagForGirlsMorelliSecond.jpg);
                            margin-bottom: 10px;
                        }
                        .postImage10 {
                            height: 150px;
                            background-size: cover;
                            background-image: url(../Assets/bags/blueBagForGirlsBlureeditionSecond.jpg);
                            margin-bottom: 10px;
                        }
                        .postContainer {
                            display: flex;
                            flex-flow: column wrap;
                            justify-content: center;
                            align-items: center;
                            h1 {
                                font-size: 16px;
                                font-weight: bold; 
                                color: black;
                            }
                            .discount {
                                color: red;
                            }
                            .price {
                                text-decoration: line-through;
                            }
                        }
                    }
                }
        }
        }
    }
