@media (max-width: 425px) {
    .wrapper {
        .filterForMobile {
            display: none;
        }
        .body {
            display: block;
            .search__body {
                .search__container {
                    .iconFilter {
                        display: inline-block;
                    }
                }
            }
        }
    }
    .filter {
        display: none;
    }
