%propertiesForText {
    font-size: 16px;
    font-weight: bold;
    color: white;
}
%borderForButtons {
    background: transparent;
    border: 1px solid white;
    border-radius: 30px;
}

.wrapper {
    width: 100%;
    height: 88%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 50px;
    .container {
        display: flex;
        flex-flow: column wrap;
        justify-content: center;
        align-items: center;
        border: 2px solid white;
        border-radius: 30px;
        padding: 10px;
        .header {
            button {
                @extend %propertiesForText;
                @extend %borderForButtons;
                padding: 5px 10px 5px 10px;
                margin: 0 10px 10px 10px;
                cursor: pointer;
                transition: 0.3s;
                &:hover {
                    border: 1px solid #f8af42;
                    color: #f8af42;
                }
            }
        }
        .body {
            .loginReg {
                margin-bottom: 8px;
                h1 {
                    @extend %propertiesForText;
                    font-size: 14px;
                    margin-bottom: 5px;
                }
                input {
                    @extend %propertiesForText;
                    @extend %borderForButtons;
                    text-align: left;
                    padding: 5px 10px 5px 10px;
                    &:focus {
                        outline: 0;
                    }
                }
            }
            .passwordReg {
                margin-bottom: 8px;
                h1 {
                    @extend %propertiesForText;
                    margin-bottom: 5px;
                    font-size: 14px;
                }
                input {
                    @extend %propertiesForText;
                    @extend %borderForButtons;
                    text-align: left;
                    padding: 5px 10px 5px 10px;
                    &:focus {
                        outline: 0;
                    }
                }
            }
            .buttonReg {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-flow: column wrap;
                button {
                    @extend %propertiesForText;
                    @extend %borderForButtons;
                    padding: 5px 10px 5px 10px;
                    cursor: pointer;
                    transition: 0.3s;
                    &:hover {
                        border: 1px solid #f8af42;
                        color: #f8af42;
                }
                }
                h1 {
                    @extend %propertiesForText;
                    margin-bottom: 5px;
                    font-size: 14px;
                }
            }
        }
    }
}