.wrapper {
    width: 100%;
    height: 88%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px; 
    .body {
        width: 100%;
        height: 100%;
        background-color: #52C7FF;
        border-radius: 30px;
        padding: 20px;
        display: flex;
        flex-flow: column wrap;
        justify-content: flex-start;
        align-items: center;
        .header {
            display: flex;
            flex-flow: row wrap;
            justify-content: center;
            align-items: center;
            .header__text {
                margin-right: 5px;
                h1 {
                    display: inline-block;
                    text-align: center;
                    color: white;
                    font-size: 24px;
                    font-weight: bold;
                }
            }
            .header__btnGoBack {
                button {
                    width: 30px;
                    height: 30px;
                    color: white;
                    background-color: #52C7FF;
                    border: 1px solid white;
                    border-radius: 12px;
                    font-size: 16px;
                    font-weight: bold;
                    padding: 5px;
                    cursor: pointer;
                    transition: 0.3s;
                    &:hover {
                        background-color: #30a1d6;
                    }
                }
            }
        }
        .container {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
            height: 95%;
            .container__image {
                display: flex;
                flex-flow: column wrap;
                align-items: center;
                justify-content: center;
                margin-left: 5%;
                .forImage {
                    border: 1px solid white;
                    height: 200px;
                    width: 200px;
                    background-size: cover;
                    background-image: url(../../Assets/bags/blueBagForGirlsBlureedition.jpeg)
                }
                h1 {
                    display: inline-flex;
                    color: white;
                    font-size: 24px;
                    font-weight: bold;
                    text-align: center;
                }
                .price {
                    margin-top: 10px;
                    text-decoration: line-through;
                }
                .discount {
                    color: red;
                }

            }
            .container__description {
                width: 100%;
                display: flex;
                flex-flow: column wrap;
                align-items: center;
                justify-content: center;
                h1 {
                    display: inline-block;
                    text-align: center;
                    color: white;
                    font-size: 24px;
                    font-weight: bold;
                }
            }
        }
    }
}

@media (max-width: 600px) {
    .wrapper {
        .body {
            .header {
                .header__text {
                    h1 {
                        font-size: 20px;
                    }
                }
            }
            .container {
                margin-top: 1%;
                display: flex;
                flex-flow: column wrap;
                justify-content: flex-start;
                align-items: center;
                .container__image {
                    margin-left: 1%;
                    .forImage {
                        height: 150px;
                        width: 150px;
                    }
                    h1 {
                        font-size: 16px;
                    }
                }
                .container__description {
                    h1 {
                        font-size: 16px;
                    }
                }
            }
        }
    }
}

@media (max-width: 420px) {
    .wrapper {
        .body {
            .header {
                .header__text {
                    h1 {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}
