.headerWrapper {
    width: 100%;
    height: 12%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    .image {
        border-radius: 50px;
        width: 90px;
        height: 90px;
        background-image: url(../Assets/sonicImage.jpg);
        background-size: cover;
        cursor: pointer;
        transition: 0.3s;
        &:hover {
            transform: rotateY(180deg);
        }
    }
    button {
        font-size: 16px;
        font-weight: bold;
        background-color: transparent;
        border: 0;
        color: white;
        cursor: pointer;
        transition: 0.3s;
        &:hover {
            color: #f8af42;
        }
    }
    .projects {
        margin-left: 50px;
        margin-right: 50px;
        border-radius: 30px;
        padding: 8px 22px 8px 22px;
    }
    .main {
        border-radius: 30px;
        padding: 8px 22px 8px 22px;
    }
}
@media (max-width: 450px) {
    .headerWrapper {
        .projects {
            margin-left: 15px;
            margin-right: 15px;
        }
    }
}
@media (max-width: 380px) {
    .headerWrapper {
        .image {
            width: 75px;
            height: 75px;
        }
        .projects {
            margin-left: 8px;
            margin-right: 8px;
        }
        button {
            font-size: 14px;
        }
    }
}
@media (max-width: 340px) {
    .headerWrapper {
        .image {
            width: 75px;
            height: 75px;
        }
        .projects {
            margin-left: 8px;
            margin-right: 8px;
        }
        button {
            font-size: 12px;
        }
    }
}