* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.wrapper {
    width: 100%;
    height: 88%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #3AB8F5;
    box-sizing: border-box;
    background-size: cover;
    .container {
        width: 70%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .header {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            .selectTypes {
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                .headerBody {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: row;
                    padding-top: 1%;
                    margin-bottom: 1%;
                    .selectTypeButton {
                        color: white;
                        background-color: rgba($color: #000000, $alpha: 0);
                        border: 3px solid white;
                        border-radius: 10px;
                        padding: 3%;
                        cursor: pointer;
                        transition: 0.3s;
                        &:hover {
                            border: 3px solid #f5deb3;
                            color: #f5deb3;
                        }
                    }
                    .stateType {
                        height: 100%;
                        border: 3px solid white;
                        background-color: rgba($color: #000000, $alpha: 0);
                        font-size: 1.5em;
                        color: white;
                        padding: 3%;
                        border-radius: 12px;
                    }
                    button:not(.stateType) {
                        height: 100%;
                        border: 3px solid white;
                        background-color: rgba($color: #000000, $alpha: 0);
                        font-size: 1.5em;
                        color: white;
                        padding: 3%;
                        cursor: pointer;
                        border-radius: 12px;
                        transition: 0.3s;
                        &:hover {
                            border: 3px solid #f5deb3;
                            color: #f5deb3;
                        }
                    }
                }
                .types {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    button {
                        width: 120%;
                        cursor: pointer;
                        transition: 0.3s;
                        &:hover {
                            background-color: rgba($color: #cfcfcf, $alpha: 1);
                        }
                    }
                    h1 {
                        font-size: 100%;
                    }
                    .normalType {
                        border: 3px solid white;
                        background-color: rgba($color: #a7a779, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .fightType {
                        border: 3px solid white;
                        background-color: rgba($color: #be332e, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .flyType {
                        border: 3px solid white;
                        background-color: rgba($color: #ac97fc, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .toxicType {
                        border: 3px solid white;
                        background-color: rgba($color: #a244a2, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .groundType {
                        border: 3px solid white;
                        background-color: rgba($color: #dfc06e, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .rockType {
                        border: 3px solid white;
                        background-color: rgba($color: #b89f42, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .bugType {
                        border: 3px solid white;
                        background-color: rgba($color: #a7b731, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .ghostType {
                        border: 3px solid white;
                        background-color: rgba($color: #5e566b, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .ironType {
                        border: 3px solid white;
                        background-color: rgba($color: #b8b8d1, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .fireType {
                        border: 3px solid white;
                        background-color: rgba($color: #fb8637, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .waterType {
                        border: 3px solid white;
                        background-color: rgba($color: #6b92ec, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .grassType {
                        border: 3px solid white;
                        background-color: rgba($color: #79c757, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .electricType {
                        border: 3px solid white;
                        background-color: rgba($color: #f7ce41, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .psychicType {
                        border: 3px solid white;
                        background-color: rgba($color: #f75c89, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .iceType {
                        border: 3px solid white;
                        background-color: rgba($color: #9ad8d6, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .dragonType {
                        border: 3px solid white;
                        background-color: rgba($color: #7043f4, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .darkType {
                        border: 3px solid white;
                        background-color: rgba($color: #70594a, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                    .fairyType {
                        border: 3px solid white;
                        background-color: rgba($color: #ec9aad, $alpha: 1);
                        color: white;
                        font-size: 1em;
                        padding: 3%;
                    }
                }
            }
        }
        .body {
            width: 100%;
            color: white;
            padding: 2%;
            button {
                background: transparent;
                border: 1px solid white;
                border-radius: 30px;
                padding: 5px 10px 5px 10px;
                color: white;
            }
            .normalTypeBtn {
                background-color: rgba($color: #a7a779, $alpha: 1);
            }
            .fightTypeBtn {
                background-color: rgba($color: #be332e, $alpha: 1);
            }
            .flyTypeBtn {
                background-color: rgba($color: #ac97fc, $alpha: 1);
            }
            .toxicTypeBtn {
                background-color: rgba($color: #a244a2, $alpha: 1);
            }
            .groundTypeBtn {
                background-color: rgba($color: #dfc06e, $alpha: 1);
            }
            .rockTypeBtn {
                background-color: rgba($color: #b89f42, $alpha: 1);
            }
            .bugTypeBtn {
                background-color: rgba($color: #a7b731, $alpha: 1);
            }
            .ghostTypeBtn {
                background-color: rgba($color: #5e566b, $alpha: 1);
            }
            .ironTypeBtn {
                background-color: rgba($color: #b8b8d1, $alpha: 1);
            }
            .fireTypeBtn {
                background-color: rgba($color: #fb8637, $alpha: 1);
            }
            .waterTypeBtn {
                background-color: rgba($color: #6b92ec, $alpha: 1);
            }
            .grassTypeBtn {
                background-color: rgba($color: #79c757, $alpha: 1);
            }
            .electricTypeBtn {
                background-color: rgba($color: #f7ce41, $alpha: 1);
            }
            .psychicTypeBtn {
                background-color: rgba($color: #f75c89, $alpha: 1);
            }
            .iceTypeBtn {
                background-color: rgba($color: #9ad8d6, $alpha: 1);
            }
            .dragonTypeBtn {
                background-color: rgba($color: #7043f4, $alpha: 1);
            }
            .darkTypeBtn {
                background-color: rgba($color: #70594a, $alpha: 1);
            }
            .fairyTypeBtn {
                background-color: rgba($color: #ec9aad, $alpha: 1);
            }
            h1 {
                display: inline;
                font-size: 150%;
            }
            .effectiveTypes {
                color: white;
            }
            .notEffectiveTypes {
                color: white;
            }
            .immuneTypes {
                color: white;
            }
        }
    }
}



@media (max-width: 760px) {
    .wrapper {
        height: 120vh;
        .container {
            width: 100%;
            .header {
                .textCountTypes {
                    h1 {
                        font-size: 200%;
                    }
                }
            }
        }
    }
}



@media (max-width: 560px) {
    .wrapper {
        .container {
            .header {
                .countTypes {
                    button {
                        font-size: 125%;
                    }
                }
                .selectTypes {
                    .headerBody {
                        button {
                            font-size: 125%;
                        }
                    }
                }
                .textCountTypes {
                    h1 {
                        font-size: 125%;
                    }
                }
            }
        }
    }
}



@media (max-width: 450px) {
    .wrapper {
        .container {
            display: flex;
            justify-content: flex-start;
        }
    }
}