%stylesText {
    color: white;
    font-size: 36px;
    font-weight: bold;
    text-align: center;
}
* {
    font-family: 'IBM Plex Sans', sans-serif;
    font-family: 'Inter', sans-serif;
    margin: 0;
    padding: 0;
}
.wrapper {
    width: 100%;
    height: 88%;
    box-sizing: border-box;
    .container {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: row wrap;
        .container__sliderLeft {
            padding: 10px;
            border: 0;
            background: transparent;
            .sliderLeftImage {
                width: 82px;
                height: 82px;
                transition: 0.3s;
                background-image: url(../Assets/salePhone/arrow.png);
                background-size: cover;
                cursor: pointer;
                &:hover {
                    background-image: url(../Assets/salePhone/arrowActive.png);
                }
            }
        }
        .container__image {
            padding: 10px;
            display: flex;
            flex-flow: column wrap;
            justify-content: center;
            align-items: center;
            .textName {
                @extend %stylesText;
                margin-bottom: 5px;
            }
            .phoneImageGalaxy {
                width: 335px;
                height: 405px;
                background-image: url(../Assets/salePhone/mobileImageSamsung.png);
                background-size: cover;
                margin-bottom: 5px;
            }
            .phoneImageXiaomi {
                width: 335px;
                height: 405px;
                background-image: url(../Assets/salePhone/mobileImageXiaomi.png);
                background-size: cover;
                margin-bottom: 5px;
            }
            .phoneImageIphone {
                width: 335px;
                height: 405px;
                background-image: url(../Assets/salePhone/mobileImageIphone.png);
                background-size: cover;
                margin-bottom: 5px;
            }
            .fullPrice {
                @extend %stylesText;
                text-decoration: line-through;
                margin-bottom: 5px;
            }
            .discountPrice {
                @extend %stylesText;
                color: #FF3131;
                margin-bottom: 5px;
            }
            .containerButtons {
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 10px;
                button {
                    @extend %stylesText;
                    background-color: transparent;
                    cursor: pointer;
                    margin: 0 10px 0 10px;
                    padding: 10px;
                    font-size: 16px;
                    border: 1px solid white;
                    border-radius: 30px;
                    transition: 0.3s;
                    &:hover {
                        border: 1px solid #e4524f;
                        color: #e4524f;
                    }
                }
            }
        }
        .container__sliderRight {
            padding: 10px;
            border: 0;
            background: transparent;
            .sliderRightImage {
                width: 82px;
                height: 82px;
                transform: rotate(180deg);
                transition: 0.3s;
                background-image: url(../Assets/salePhone/arrow.png);
                background-size: cover;
                cursor: pointer;
                &:hover {
                    background-image: url(../Assets/salePhone/arrowActive.png);
                }
            }
        }
    }
}

@media (max-width: 590px) {
    .wrapper {
        .container {
            .container__sliderLeft {
                padding: 0;
                .sliderLeftImage {
                    width: 41px;
                    height: 41px;
                }
            }
            .container__image {
                padding: 0px;
                .textName {
                    font-size: 16px;
                }
                .phoneImageGalaxy {
                    width: 167.5px;
                    height: 202.5px;
                    background-image: url(../Assets/salePhone/mobileImageSamsung.png);
                    background-size: cover;
                    margin-bottom: 5px;
                }
                .phoneImageXiaomi {
                    width: 167.5px;
                    height: 202.5px;
                    background-image: url(../Assets/salePhone/mobileImageXiaomi.png);
                    background-size: cover;
                    margin-bottom: 5px;
                }
                .phoneImageIphone {
                    width: 167.5px;
                    height: 202.5px;
                    background-image: url(../Assets/salePhone/mobileImageIphone.png);
                    background-size: cover;
                    margin-bottom: 5px;
                }
                .phoneImage {
                    width: 167.5px;
                    height: 202.5px;
                }
                .fullPrice {
                    font-size: 20px;
                }
                .discountPrice {
                    font-size: 20px;
                }
            }
            .container__sliderRight {
                padding: 0;
                .sliderRightImage {
                    width: 41px;
                    height: 41px;
                }
            }   
        }
    }
}