.portfolioWrapper {
    width: 100%;
    height: 88%;
    box-sizing: border-box;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    .container {
        width: 80%;
        display: flex;
        flex-flow: column nowrap;
        justify-content: flex-start;
        align-items: center;
        .text {
            color: white;
            margin: 50px 0 50px 0;
            h1 {
                font-size: 36;
                font-weight: bold;
            }
        }
        .stack {
            .htmlIcon {
                display: inline-block;
                width: 100px;
                height: 100px;
                background-image: url(../Assets/Htmlicon.png);
                background-size: cover;
                margin: 0 10px 0 10px;
            }
            .cssIcon {
                display: inline-block;
                width: 100px;
                height: 100px;
                background-image: url(../Assets/CSSicon.png);
                background-size: cover;
                margin: 0 10px 0 10px;
            }
            .jsIcon {
                display: inline-block;
                width: 100px;
                height: 100px;
                background-image: url(../Assets/JSicon.png);
                background-size: cover;
                margin: 0 10px 0 10px;
            }
            .stackText {
                display: inline-block;
                margin: 0 10px 0 10px;
                h1 {
                    border: 3px solid white;
                    border-radius: 35px;
                    padding: 15px 20px 15px 20px;
                    font-size: 16px;
                    font-weight: bold;
                    text-align: center;
                    color: white;
                }
            }
        }
        .location {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 50px;
            .locationIcon {
                width: 100px;
                height: 100px;
                background-image: url(../Assets/Location.png);
                background-size: cover;
            }
            .locationText {
                margin: 0 10px 0 10px;
                h1 {
                    border: 3px solid white;
                    border-radius: 35px;
                    padding: 15px 20px 15px 20px;
                    font-size: 16px;
                    font-weight: bold;
                    text-align: center;
                    color: white;
                }
            }
        }
    }
}
@media (max-width: 768px) {
    .portfolioWrapper {
        .container {
            .stack {
                .htmlIcon {
                    display: none;
                }
                .cssIcon {
                    display: none;
                }
                .jsIcon {
                    display: none;
                }
            }
            .location {
                .locationIcon {
                    display: none;
                }
            }
        }
    }
}
@media (max-width: 490px) {
    .portfolioWrapper {
        .container {
            .text {
                h1 {
                    font-size: 24px;
                }
            }
        }
    }
}
@media (max-width: 375px) {
    .portfolioWrapper {
        .container {
            .text {
                h1 {
                    font-size: 18px;
                }
            }
        }
    }
}