.wrapper {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
    width: 100vw;
    height: 88vh;
    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: column wrap;
        .countText {
            h1 {
                text-align: center;
                font-weight: bold;
                color: white;
                font-size: 36px;
                margin-bottom: 10px;
            }
        }
        .countButtons {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-flow: row wrap;
            button {
                font-weight: bold;
                color: white;
                background-color: transparent;
                cursor: pointer;
                border: 1px solid white;
                border-radius: 30px;
                font-size: 36px;
                margin: 0 10px 0 10px;
                padding: 0 25px 0 25px;
                transition: 0.3s;
                &:hover {
                    border: 1px solid #50FF41; 
                    color: #50FF41;
                }
            }
        }
    }
}