.projectsWrapper {
    width: 100%;
    height: 88%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    .container {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        display: block;
        button {
            width: 185px;
            display: inline-block;
            background-color: transparent;
            border: 3px solid white;
            border-radius: 30px;
            padding: 15px;
            margin: 15px 65px 15px 65px;
            cursor: pointer;
            color: white;
            transition: 0.3s;
            &:hover {
                border: 3px solid #f8af42;
                color: #f8af42;
            }
            h1 {
                
                font-size: 16px;
                font-weight: bold;
            }
        }
    }
}

@media (max-width: 1440px) {
    .projectsWrapper {
        .container {
            display: block;
            button {
                margin: 15px 50px 15px 50px;
            }
        }
    }
}

@media (max-width: 1024px) {
    .projectsWrapper {
        .container {
            display: block;
            button {
                margin: 15px 35px 15px 35px;
            }
        }
    }
}

@media (max-width: 870px) {
    .projectsWrapper {
        .container {
            padding: 0;
            display: flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            align-items: center;
            button {
                margin: 15px 10px 15px 10px;
                }
        }
    }
}