.predictorRouletteWrapper {
    width: 100%;
    height: 88%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    .container {
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: column wrap;
        border: 3px solid white;
        border-radius: 30px;
        padding: 10px 40px 10px 40px;
        margin-top: 25px;
        .header {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 10px;
            input {
                color: white;
                background: transparent;
                border: 1px solid white;
                border-radius: 30px;
                font-size: 16px;
                font-weight: bold;
                text-align: center;
                padding: 3px 0 3px 0;
                &:focus {
                    outline: none;
                }
            }
            .leftInput {
                margin-right: 15px;
            }
            .rightInput {
                margin-left: 15px;
            }
        }
        .countAttempts {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 10px;
            h1 {
                width: 100%;
                text-align: center;
                display: inline-block;
                color: white;
                font-size: 16px;
                font-weight: bold;
                border: 1px solid white;
                border-radius: 30px;
                padding: 1px 50px 1px 50px;
                margin-right: 5px;
            }
            button {
                color: white;
                font-size: 16px;
                font-weight: bold;
                background: transparent;
                border: 1px solid white;
                border-radius: 30px;
                padding: 3px 15px 3px 15px;
                margin-left: 5px;
                cursor: pointer;
                transition: 0.3s;
                &:hover {
                    color: #f8af42;
                    border: 1px solid #f8af42;
                }
            }
        }
        .typesButtons {
            width: 100%;
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin-bottom: 10px;
            h1 {
                display: inline-block;
                text-align: center;
                font-size: 16px;
                font-weight: bold;
                padding: 3px 15px 3px 15px;
            }
            .black {
                border: 1px solid black;
                border-radius: 30px;
                color: black;
            }
            .orange {
                border: 1px solid #FFC700;
                border-radius: 30px;
                color: #FFC700;
            }
            .red {
                border: 1px solid #FF5151;
                border-radius: 30px;
                color: #FF5151;
            }
            .green {
                border: 1px solid #50FF41;
                border-radius: 30px;
                color: #50FF41;
            }
        }
        .zeroingOut {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 10px;
            button {
                width: 100%;
                text-align: center;
                font-size: 16px;
                font-weight: bold;
                color: white;
                background: transparent;
                border: 1px solid white;
                border-radius: 30px;
                padding: 3px 5px 3px 5px;
                margin-right: 30px;
                cursor: pointer;
                transition: 0.3s;
                &:hover {
                    color: #f8af42;
                    border: 1px solid #f8af42;
                }
            }
            h1 {
                width: 100%;
                display: inline-block;
                text-align: center;
                font-size: 16px;
                font-weight: bold;
                color: white;
                border: 1px solid white;
                border-radius: 30px;
                padding: 2px 5px 2px 5px;
                margin-left: 30px;
                transition: 0.3s;
            }
            .greenStateCounts {
                color: #50FF41;
                border: 1px solid #50FF41;
                transition: 0.3s;
            }
        }
        .play {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            button {
                width: 100%;
                text-align: center;
                font-size: 16px;
                font-weight: bold;
                color: white;
                background: transparent;
                border: 1px solid white;
                border-radius: 30px;
                margin-right: 30px;
                padding: 5px 3px 5px 3px;
                cursor: pointer;
                transition: 0.3s;
                &:hover {
                    color: #f8af42;
                    border: 1px solid #f8af42;
                }
            }
            h1 {
                border: 1px solid white;
                border-radius: 30px;
                text-align: center;
                font-size: 16px;
                margin-left: 30px;
                padding: 5px 36px 5px 36px;
                font-weight: bold;
                color: white;
            }
            .blackState {
                color: black;
                border: 1px solid black;
                transition: 0.3s;
            }
            .orangeState {
                color: #FFC700;
                border: 1px solid #FFC700;
                transition: 0.3s;
            }
            .redState {
                color: #FF5151;
                border: 1px solid #FF5151;
                transition: 0.3s;
            }
            .greenState {
                color: #50FF41;
                border: 1px solid #50FF41;
                transition: 0.3s;
            }
        }
    }
}

@media (max-width: 565px) {
    .predictorRouletteWrapper {
        .container {
            padding: 10px 5px 10px 5px;
            .header {
                width: 75%;
                input {
                    width: 75%;
                }
            }
            .countAttempts {
                width: 75%;
                h1 {
                    width: 75%;
                    padding: 1px 10px 1px 10px;
                }
                button {
                    width: 75%;
                }
            }
            .typesButtons {
                width: 75%;
                h1 {
                    width: 75%;
                }
            }
            .zeroingOut {
                width: 75%;
                button {
                    width: 75%;
                }
            }
            .play {
                width: 75%;
                h1 {
                    width: 75%;
                }
            }
        }
    }
}

@media (max-width: 485px) {
    .predictorRouletteWrapper {
        .container {
            width: 95%;
            padding: 10px 5px 10px 5px;
            .header {
                width: 100%;
                input {
                    width: 100px;
                    padding: 3px;
                    font-size: 12px;
                }
                .leftInput {
                    margin-right: 5px;
                }
                .rightInput {
                    margin-left: 5px;
                }
            }
            .countAttempts {
                width: 100%;
                h1 {
                    width: 100%;
                    padding: 1px 10px 1px 10px;
                    font-size: 12px;
                }
                button {
                    font-size: 12px;
                }
            }
            .typesButtons {
                width: 100%;
                h1 {
                    width: 100%;
                    font-size: 12px;
                }
            }
            .zeroingOut {
                width: 100%;
                button {
                    width: 100%;
                    font-size: 12px;
                    margin-right: 5px;
                }
                h1 {
                    font-size: 12px;
                    margin-left: 5px;
                }
            }
            .play {
                width: 100%;
                button {
                    width: 100%;
                    margin-right: 5px;
                }
                h1 {
                    width: 30%;
                    font-size: 12px;
                    margin-left: 5px;
                    padding: 6px 5px 6px 5px;
                }
            }
        }
    }
}